<script>
   /*************************************************************/
   /* PlotDistance for PCA results                              */
   /* ----------------------------                              */
   /* shows plot with score and orthogonal distances            */
   /* and acceptance areas  (both for objects and variables)    */
   /*************************************************************/

   import { Lines, Legend } from 'svelte-plots-basic/2d';
   import { pcares_get } from '..//wasm/dist/pca.js';

   import { getDistanceCoords, getDistanceAxisLabel, getDistanceLimitCoordinates, getResLegend,
      MAX_LABEL_SYMBOLS, LINE_COLORS, FACE_TCOLORS, MARKERS} from './Defaults.js';
   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   // get the parameters needed for the plot
   let data = $derived(props && props.data ? props.data : null);
   let res = $derived(props && props.res ? props.res : null);

   // get results and datasets (second objects will be available only if user selected 'both')
   const resIDs = $derived(res && params ? (params.resID === 'both' ? Object.keys(res) : [params.resID]) : null);
   const resobj1 = $derived(resIDs ? res[resIDs[0]] : null);
   const resobj2 = $derived(resIDs && resIDs.length > 1 ? res[resIDs[1]] : null);
   const dataset1 = $derived(resIDs ? data[resIDs[0]] : null);

   // constant plot parameters
   const LINE_COLOR = '#909090';    // color of the limits lines
   const NPOINTS = 100;             // number of points in limit lines

   // get specific type of limits
   let N = $derived(params.limType === 'classic' ?
      {h: resobj1['cNh'], q: resobj1['cNq']} :
      {h: resobj1['rNh'], q: resobj1['rNq']}
   );

   let u0 = $derived(params.limType === 'classic' ?
      {h: resobj1['ch0'], q: resobj1['cq0']} :
      {h: resobj1['rh0'], q: resobj1['rq0']}
   );

   // get limit values for specific number of components
   let Na = $derived({h: N.h[params.ncomp - 1], q: N.q[params.ncomp - 1]});
   let u0a = $derived({h: u0.h[params.ncomp - 1], q: u0.q[params.ncomp - 1]});

   // get conf limits
   let ecl = $derived(params.alpha ? 1 - params.alpha/100 : 0.95);
   let ocl = $derived(params.gamma ? 1 - params.gamma/100 : 0.99);

   // get coordinates for limits
   let els = $derived(getDistanceLimitCoordinates(ecl, Na.h, Na.q, params.transType));
   let ols = $derived(getDistanceLimitCoordinates(Math.pow(ocl, 1/resobj1.nrows), Na.h, Na.q, params.transType));

   // reactive expression to compute colorby parameters
   let h1 = $derived(resobj1 ? pcares_get(resobj1, "hdist", params.ncomp): null);
   let q1 = $derived(resobj1 ? pcares_get(resobj1, "qdist", params.ncomp): null);

   let h2 = $derived(resobj2 ? pcares_get(resobj2, "hdist", params.ncomp): null);
   let q2 = $derived(resobj2 ? pcares_get(resobj2, "qdist", params.ncomp): null);

   // compute coordinates and labels
   let xc1 = $derived(getDistanceCoords(h1, Na.h, u0a.h, params.transType));
   let yc1 = $derived(getDistanceCoords(q1, Na.q, u0a.q, params.transType));
   let labels1 = $derived(resobj1 ? resobj1.objlabels.map(v => v.slice(0, MAX_LABEL_SYMBOLS)) : null);

   let xc2 = $derived(h2 ? getDistanceCoords(h2, Na.h, u0a.h, params.transType) : null);
   let yc2 = $derived(q2 ? getDistanceCoords(q2, Na.q, u0a.q, params.transType) : null);
   let labels2 = $derived(resobj2 ? resobj2.objlabels.map(v => v.slice(0, MAX_LABEL_SYMBOLS)) : null);

   // derive plot parameters
   let title = $derived(params.title ? params.title : 'Distances (A = ' + params.ncomp + ')');
   let xLabel = $derived(getDistanceAxisLabel('h', params.transType));
   let yLabel = $derived(getDistanceAxisLabel('q', params.transType));

   // ranges of values to define coordinate limits
   let xRange = $state(), yRange = $state();


   let limX = $derived.by(() => {
      if (!xc1 || !xRange) return [0, 1];
      const xMaxTot1 = Math.max(xc1.max, ols.x.v[NPOINTS - 1]);
      const xMaxTot2 = xc2 ? Math.max(xc2.max, ols.x.v[NPOINTS - 1]) : 0;
      const xMaxTot = Math.max(xMaxTot1, xMaxTot2);
      return [-0.02 * (xMaxTot - xRange[0]), 1.05 * xMaxTot];
   });

   let limY = $derived.by(() => {
      if (!yc1 || !yRange) return [0, 1];
      const yMaxTot1 = Math.max(yc1.max, ols.y.v[0]);
      const yMaxTot2 = yc2 ? Math.max(yc2.max, ols.y.v[0]) : 0;
      const yMaxTot = Math.max(yMaxTot1, yMaxTot2);
      return [-0.02 * (yMaxTot - yRange[0]), (1.05 + 0.10 * (params.colorBy !== 'none')) * yMaxTot];
   });

   // legend
   const legendItems = $derived(getResLegend(resIDs));

</script>

{#if xc1 && yc1}
<Axes {title} {limX} {limY} {xLabel} {yLabel} fileName='plot-distance' onclick={callbacks.unselectObject}>

   <!-- limits  -->
   {#if els }
   <Lines xValues={els.x} yValues={els.y} lineColor={LINE_COLOR} lineType={2} />
   {/if}

   {#if ols && params.resID && params.resID !== 'alt'}
   <Lines xValues={ols.x} yValues={ols.y} lineColor={LINE_COLOR} lineType={3} />
   {/if}

   <!-- data points 1 -->
   <PlotSeries
      labels={labels1}
      {selected}
      {callbacks}
      xValues={xc1.values}
      yValues={yc1.values}
      plotType='p'
      showLabels={params.showLabels}
      marker={MARKERS[resIDs[0]]}
      mainColor={LINE_COLORS[resIDs[0]]}
      faceColor={FACE_TCOLORS[resIDs[0]]}
      bind:xRange={xRange}
      bind:yRange={yRange}
   />

   <!-- data points 2 -->
   {#if xc2 && yc2}
   <PlotSeries
      labels={labels2}
      {selected}
      {callbacks}
      xValues={xc2.values}
      yValues={yc2.values}
      plotType='p'
      showLabels={params.showLabels}
      marker={MARKERS[resIDs[1]]}
      mainColor={LINE_COLORS[resIDs[1]]}
      faceColor={FACE_TCOLORS[resIDs[1]]}
      bind:xRange={xRange}
      bind:yRange={yRange}
   />
   {/if}

   <Legend items={legendItems} position="bottomright" />


</Axes>
{/if}
