<script>
   /*************************************************************/
   /* ModelTable                                                */
   /* -------------------------                                 */
   /* shows table with PCA outcomes for training set            */
   /*************************************************************/

   import { Container, Select } from 'svelte-controls-basic';

   // main properties of the whole app
   let { res } = $props();

   const NCOMPMAX = 16;
   const resIDs = $derived(res ? Object.keys(res) : null);
   let resID = $state(resIDs[0]);
   const resobj = $derived(res ? res[resID] : null);

   const cumexpvar = $derived(resobj ? resobj.stat.cumexpvar : null);
   const expvar = $derived(resobj ? resobj.stat.expvar : null);
   const eigenvals = $derived(resobj ? resobj.eigenvals.map(v => v * (resobj.nrows - 1)) : null);
</script>


{#if resobj}
<table>
   <thead>
   <tr>
      <th>PC</th>
      <th>Expvar</th>
      <th>Cumexpvar</th>
      <th>Eigenvals</th>
   </tr>
   </thead>
   <tbody>
   {#each resobj.stat.components.filter(v => v < NCOMPMAX) as a}
   <tr>
      <td>{a}</td>
      <td>{(expvar[a - 1] * 100).toFixed(2)}</td>
      <td>{(cumexpvar[a - 1] * 100).toFixed(2)}</td>
      <td>{(eigenvals[a - 1]).toFixed(2)}</td>
   </tr>
   {/each}

   {#if resobj.ncomp > NCOMPMAX}
   <tr>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
   </tr>
   {/if}

   {#if resobj.ncomp >= NCOMPMAX}
   <tr>
      <td>{resobj.ncomp}</td>
      <td>{((cumexpvar[resobj.ncomp - 1] - cumexpvar[resobj.ncomp - 2]) * 100).toFixed(2)}</td>
      <td>{(cumexpvar[resobj.ncomp - 1] * 100).toFixed(2)}</td>
      <td>{(eigenvals[resobj.ncomp - 1]).toFixed(2)}</td>
   </tr>
   {/if}
   </tbody>
   </table>
   {#if resIDs && resIDs.length > 1}
   <Container name="res-switcher">
      <Select options={resIDs} bind:value={resID} />
   </Container>
   {/if}
{/if}

<style>

   :global(.control-element.res-switcher) {
      margin-top: 1em;
      margin-bottom: 0em;
      padding-bottom: 0em;
      justify-content: center;
      background: transparent;
      font-size: 0.8rem;
   }

   :global(.control-element.res-switcher:hover) {
      background: transparent;
   }

   :global(.control-element.res-switcher .selector) {
      justify-content: center;
   }

   :global(.control-element.res-switcher > .selector > .option) {
      background-color: #e8e8e8;
      border-color: #708090;
      color: #607080;
   }

   :global(.control-element.res-switcher > .selector > .option:not(.selected):hover) {
      background-color: #fffef0;
      color: #607080;
      border-color: #607080;
   }

   :global(.control-element.res-switcher > .selector > .option.selected) {
      background-color: #607080;
      color: #e8f0f8;
      border-color: #607080;
   }

   table {
      width: 100%;
      border-collapse: collapse;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.2em 0.5em 0.2em 0.1em;
      color: #404040;
      margin: 0;
   }
</style>