<script>
   /*************************************************************/
   /* PlotVariance                                              */
   /* --------------------                                      */
   /* shows plot with variance values vs. number of PCs         */
   /*************************************************************/

   import { XAxis, YAxis, Box, Legend } from 'svelte-plots-basic/2d';
   import { vector } from 'mdatools/arrays';
   import { min } from 'mdatools/stat';
   import { getResLegend, LINE_COLORS, MARKERS, FACE_COLORS } from './Defaults.js';

   import PlotSeries from './PlotSeries.svelte';
   import Axes from './Axes.svelte';

   let {
      params,                        // JSON with main plot parameters
      callbacks,                     // JSON with all callback methods
      props,                         // JSON with main properties (model, results, dataset)
      selected,                      // ID of selected object
   } = $props();

   // get the parameters needed for the plot
   let res = $derived(props ? props.res : null);

   // get results and datasets (second objects will be available only if user selected 'both')
   const resIDs = $derived(res && params ? (params.resID === 'both' ? Object.keys(res) : [params.resID]) : null);
   const resobj1 = $derived(resIDs ? res[resIDs[0]] : null);
   const resobj2 = $derived(resIDs && resIDs.length > 1 ? res[resIDs[1]] : null);


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const titles = {
      'expvar': 'Explained variance',
      'resvar': 'Residual variance',
      'cumexpvar': 'Cumulative explained variance',
      'cumresvar': 'Cumulative residual variance'
   };

   const varForm = $derived(params.varForm === 'cumulative' ? 'cum' : '');
   const varType = $derived(params.varType === 'explained' ? 'exp' : 'res');
   const statName = $derived(varForm + varType + 'var');
   const title = $derived(titles[statName]);

   const xMin = $derived(resobj1.ncomp > 1 ? 1 - resobj1.ncomp * 0.065 : -0.1);
   const xMax = $derived(resobj1.ncomp > 1 ? resobj1.ncomp * 1.065 : 2.1);
   const limX = $derived([xMin, xMax]);

   const xValues1 = $derived(resobj1 ? resobj1.stat.components : null);
   const yValues1 = $derived(resobj1 ? vector(resobj1.stat[statName]) : null);

   const xValues2 = $derived(resobj2 ? resobj2.stat.components : null);
   const yValues2 = $derived(resobj2 ? vector(resobj2.stat[statName]) : null);

   const labels1 = $derived(yValues1 ? Array.from(yValues1.v).map(v => v < 0.1 ? v.toFixed(3) : v.toFixed(2)) : null);
   const labels2 = $derived(yValues2 ? Array.from(yValues2.v).map(v => v < 0.1 ? v.toFixed(3) : v.toFixed(2)) : null);
   const minY1 = $derived(yValues1 ? [Math.min(-0.1, min(yValues1))] : -0.1);
   const minY2 = $derived(yValues2 ? [Math.min(-0.1, min(yValues2))] : -0.1);
   const limY = $derived(minY1 && minY2 ? [Math.min(minY1, minY2), 1.1] : [0,1.1]);

   // legend
   const legendItems = $derived(getResLegend(resIDs));
</script>

{#if xValues1 && yValues1 }
<Axes {title} {limX} {limY} fileName={'plot-' + statName} whole={true}>

   <!-- data points 1 -->
   <PlotSeries
      xValues={xValues1}
      yValues={yValues1}
      labels={labels1}
      nBars={resIDs.length}
      barNum={1}
      plotType={params.plotType}
      showLabels={params.showLabels}
      mainColor={LINE_COLORS[resIDs[0]]}
      faceColor={FACE_COLORS[resIDs[0]]}
      marker={MARKERS[resIDs[0]]}
      lineWidth={1.5}
   />

   <!-- data points 2 -->
   {#if xValues2 && yValues2}
   <PlotSeries
      xValues={xValues2}
      yValues={yValues2}
      labels={labels2}
      nBars={resIDs.length}
      barNum={2}
      plotType={params.plotType}
      showLabels={params.showLabels}
      mainColor={LINE_COLORS[resIDs[1]]}
      faceColor={FACE_COLORS[resIDs[1]]}
      marker={MARKERS[resIDs[1]]}
      lineWidth={1.5}
   />
   {/if}

   <Legend items={legendItems} position="bottomright"/>

   <XAxis showGrid={true} label='Number of PCs' whole={true} />
   <YAxis showGrid={true} label='Variance' />
   <Box />
</Axes>
{/if}