<script>
   /*************************************************************/
   /* Root component for the application                        */
   /*************************************************************/

   import { date2str } from '../../shared/utils';

   import TrainAppContainer from './tab_train/AppContainer.svelte';
   import PredictAppContainer from './tab_new/AppContainer.svelte';
   import App from '../../shared/components/MainApp.svelte';
   import WASM from '../../shared/wasm/dist/pca_wasm.js';

   /* callback for reseting the app via session */
   function resetSession(s) {
      s.train.calset = null;
      s.new.model = null;
      s.tab = 0;
   }

   /* callback to check validity of JSON with session */
   function checkSession(s) {
      const keys1 = Object.keys(s);
      if (!['date', 'tab', 'train', 'new'].every(v => keys1.includes(v))) return false;

      const keys2 = Object.keys(s.train);
      if (!['model', 'calset', 'calsetp', 'pvset', 'calres', 'pvres',  'outliers', 'prepType',
         'prepmodel', 'prepmeta', 'prepobj'].every(v => keys2.includes(v))) return false;
      const keys3 = Object.keys(s.new);
      if (!['model', 'newset', 'newres',  'outliers'].every(v => keys3.includes(v))) return false;
      return true;
   }

   /* callback to initialize new session */
   function initSession() {
      return {
         date: date2str(new Date()),
         tab: 0,
         train: {
            model : 0,
            calset: 0,
            calsetp: 0,
            pvset: 0,
            calres: 0,
            pvres: 0,
            outliers: [],
            prepType: null,
            prepmodel: 0,
            prepmeta: 0,
            prepobj: [],
         },
         new: {
            model : 0,
            newset: 0,
            newres: 0,
            outliers: []
         }
      }
   }

   // app containers
   const tabs = [
      {name: 'Train', label: 'train', component: TrainAppContainer},
      {name: 'Project', label: 'new', component: PredictAppContainer},
   ]
</script>

<App
   appID="pca"
   appTitle="PCA"
   appShortTitle="PCA"
   appSlogan="Principal component analysis"
   {WASM}
   {tabs}
   {initSession}
   {checkSession}
   {resetSession}
>

   {#snippet footer()}
      <p>Current release: <span>2.1.6</span>
      Feedback: <a class="paper-link" href="mailto:help@mda.tools?subject=PCA app">help@mda.tools</a>
      Tutorial: <a class="paper-link" href="/pca/Wines.zip" download>dataset</a>
      | <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFmpfjgu7n4y-slf8AAN74xC">video lessons</a>
      </p>
   {/snippet}

   {#snippet info()}
   <h2>Principal Component Analysis</h2>
   <p>Authors: <em>Sergey Kucheryavskiy, Oxana Rodionova, Alexey Pomerantsev</em></p>
   <p>
      Interactive web-application for creating and using PCA models for data exploration
      directly in your browser. The app does not send any information to the server,
      all calculations are done locally on your device.
   </p>
   <p>Check out YouTube <a href="https://www.youtube.com/playlist?list=PLep7KM9cEyFmpfjgu7n4y-slf8AAN74xC">video-lessons</a></p>
   <h3>v. 2.1.6</h3>
   <p class="release-info">released 30/11/2025</p>
   <ul>
      <li>Fixed several small bugs and improved app performance.</li>
   </ul>
   <h3>v. 2.1.5</h3>
   <p class="release-info">released 4/08/2025</p>
   <ul>
      <li>Updated link to video-tutorial.</li>
   </ul>
   <h3>v. 2.1.4</h3>
   <p class="release-info">released 1/08/2025</p>
   <ul>
      <li>Fixed issue with data loader when trim trails is used in preprocessing.</li>
   </ul>
   {/snippet}
</App>

